import React, { Component } from 'react';
import './Info.css';

export default class Info extends Component {
  render () {
    return (
      <div className="info-container">
        <h1>Overview</h1>
        <ul class="overview-list">
          <li className="item">Uses the same downloadable JSON configuration file as the original <a href="http://vincentgarreau.com/particles.js/">particles.js</a>.</li>
          <li className="item">Multiple particles components can be added to your page using seperate configuration files.</li>          
          <li className="item">The config prop is optional, however, you'll get the default "React Particles" until you pass one.</li>          
          <li className="item">The id prop is mandatory - it will crash &amp; burn without it.</li>
          <li className="item">Your config file should be placed in your public folder or a directory within the public folder.</li>
          <li className="item">The browser has to account for every particle and starts to bog down after it gets loaded up.</li>
          <li className="item">Each Particles component inherits its parent's height &amp; width.</li>
        </ul>
      </div>
    );
  }
}



