import React, { Component } from 'react';
import Particles from 'reactparticles.js';
import Tile1 from './Tile-1';
import Tile2 from './Tile-2';
import Tile3 from './Tile-3';
import Info from './Info';
import QuickStart from './Quick-Start';
import './Overview.css';

export default class Overview extends Component {
  render () {
    return (
      <div className="overview-container">
        <Particles id="overview"/>
        <p className="title">Independently Configurable {<br/>}
          <span className="subtitle">React component based on particles.js by vincentgarreau.</span>
        </p>
        <div className="tiles-container">
          <Tile1/>
          <Tile2/>
          <Tile3/>
        </div>
        <Info/>
        <QuickStart/>
      </div>
    );
  }
}


