import React, { Component } from 'react';
import public_folder from '../references/public_folder.png';
import './Quick-Start.css';

export default class QuickStart extends Component {
  render () {
    return (
      <div className="quick-start-container">
        <h1>Quick Start</h1>
        <h2>Install</h2>
        <div className="install">
          <code>npm install reactparticles.js</code><br/>
          or
          <br/><code>yarn add reactparticles.js</code>
        </div>
        <br/>
        <h2>Basic Usage</h2>
        <ol className="quick-start-list">
          <li className="quick-item">Import it</li>
          <li className="quick-item">Place it in a container or component</li>
          <li className="quick-item">Pass it a unique id prop</li>
          <li className="quick-item">Pass it a config file (optional)</li>
        </ol>
        <pre><code className="jsx language-jsx">import React, { "{ Component }" } from 'react';{"\n"}import Particles from 'reactparticles.js';{"\n"}{"\n"}export default class YourComponent extends Component {"{"}{"\n"}{"  "}render () {"{"}{"\n"}{"    "}return ({"\n"}{"      "}&lt;div className="your-component-container"&gt;{"\n"}{"        "}&lt;Particles id="your-component-particles"/&gt;{"\n"}{"      "}&lt;/div&gt;{"\n"}{"    "});{"\n"}{"  "}{"}"}{"\n"}{"}"}{"\n"}</code></pre>
        <h2>Using Multiple Configurations</h2>
        <ol className="quick-start-list">
          <li className="quick-item">Import</li>
          <li className="quick-item">Place each in unique container or component</li>
          <li className="quick-item">Pass each a unique id prop</li>
          <li className="quick-item">Pass each a config file</li>
        </ol>
        <pre><code className="jsx language-jsx">import React, { "{ Component }" } from 'react';{"\n"}import Particles from 'reactparticles.js';{"\n"}{"\n"}export default class Example extends Component {"{"}{"\n"}{"  "}render () {"{"}{"\n"}{"    "}return ({"\n"}{"      "}&lt;div className="tiles-container"&gt;{"\n"}{"        "}&lt;div className="tile-1"&gt;{"\n"}{"          "}&lt;Particles {"\n"}{"            "}id="tile-1-particles"{"\n"}{"            "}config="particles/tile-1-particles.json"{"\n"}{"          "}/&gt;{"\n"}{"        "}&lt;/div&gt;{"\n"}{"        "}&lt;div className="tile-2"&gt;{"\n"}{"          "}&lt;Particles {"\n"}{"            "}id="tile-2-particles"{"\n"}{"            "}config="particles/tile-2-particles.json"{"\n"}{"          "}/&gt;{"\n"}{"        "}&lt;/div&gt;{"\n"}{"        "}&lt;div className="tile-3"&gt;{"\n"}{"          "}&lt;Particles {"\n"}{"            "}id="tile-3-particles"{"\n"}{"            "}config="particles/tile-3-particles.json"{"\n"}{"          "}/&gt;{"\n"}{"        "}&lt;/div&gt;{"\n"}{"        "}&lt;div className="tile-4"&gt;{"\n"}{"          "}&lt;Particles {"\n"}{"            "}id="tile-4-particles"{"\n"}{"            "}config="particles/tile-4-particles.json"{"\n"}{"          "}/&gt;{"\n"}{"        "}&lt;/div&gt;{"\n"}{"      "}&lt;/div&gt;{"\n"}{"    "});{"\n"}{"  "}{"}"}{"\n"}{"}"}{"\n"}</code></pre>
        <h2 id="pictext">Public folder structure for multiple configurations</h2>
        <img className="pic" src={public_folder} alt="public_folder.png" width="400px" />
        <br/>
        <p className="readme">For a more detailed guide check out the <a href="https://github.com/kr1tzb1tz/reactparticles.js">README</a>.</p>
      </div>
    );
  }
}



